
import { defineComponent } from "vue";
import Footer from "@/components/Footer.vue";
import * as API from "../../API";

export default defineComponent({
  components: {
    Footer,
  },
  data() {
    return {
      expenses: [] as API.Expense[],
    };
  },
  async mounted() {
    this.expenses = await API.getExpenses();
  },
});
