
import { defineComponent } from "vue";
import * as API from "../../API";

export default defineComponent({
  data() {
    return {
      register: false,
      email: "",
      password: "",
      confirmPassword: "",
      username: "",
      error: "",
      loading: false,
    };
  },
  methods: {
    async login() {
      try {
        this.loading = true;
        if (await API.login(this.email, this.password)) this.$router.push("/");
      } catch (e) {
        this.error = "Du konntest nicht angemeldet werden ";
      } finally {
        this.loading = false;
      }
    },
    async createAcc() {
      if (this.password !== this.confirmPassword) {
        this.error = "Die passwörter stimmen nicht überein";
        return;
      }
      try {
        this.loading = true;
        await API.register(this.email, this.password, this.username);
        this.$router.push("/");
      } catch (e) {
        this.error = "Du konntest nicht registriert werden ";
      } finally {
        this.loading = false;
      }
    },
  },
});
