
import { defineComponent } from "vue";
import Footer from "@/components/Footer.vue";
import * as API from "../../API";
export default defineComponent({
  components: {
    Footer,
  },
  data() {
    return {};
  },
  methods: {
    async logout() {
      try {
        await API.logout();
        this.$router.push("/login");
      } catch (e) {
        console.error(e);
      }
    },
  },
});
